import React, { forwardRef } from "react";
import styled, { css } from "styled-components";
import MuiButtonBase from "@material-ui/core/ButtonBase";

import { DISPLAY, DotsSpinner, Icon, SPACER } from "../..";
import { ButtonBasePropTypes, ButtonBaseDefaultProps } from "./props";

const StyledButton = styled(MuiButtonBase)`
  && {
    box-sizing: border-box;
    text-transfrom: none !important;
    border-radius: 24px;
    ${({ noPadding, theme }) =>
      !noPadding && `padding: ${theme.spacing(4, 4)};`}
    min-width: 40px;
    height: 40px;
    transition: all 100ms ease-in-out;
    width: ${({ block }) => (block ? "100%" : "initial")};

    :hover {
      opacity: 0.7;
    }

    :disabled {
      opacity: 0.3;
      cursor: not-allowed;
      pointer-events: none;
    }

    ${({ size, theme }) => {
      if (size === "xs") {
        return css`
          font-size: ${theme.typography.button.fontSizeXs};
          padding: ${theme.spacing(2, 4)};
          height: 24px;
          min-width: 24px;
          border-radius: 12px;
        `;
      }

      if (size === "sm") {
        return css`
          font-size: ${theme.typography.button.fontSizeSm};
          padding: ${theme.spacing(2, 4)};
          height: 32px;
          min-width: 32px;
          border-radius: 16px;
        `;
      }

      if (size === "lg") {
        return css`
          font-size: ${theme.typography.button.fontSizeLg};
          padding: ${theme.spacing(3, 6)};
          height: 48px;
          min-width: 48px;
          border-radius: 24px;
        `;
      }

      return css`
        font-size: ${theme.typography.button.fontSizeMd};
      `;
    }}

    ${(theme) => DISPLAY(theme)}
    ${(theme) => SPACER(theme)}
  }
`;

const getIconSize = (buttonSize) => {
  switch (buttonSize) {
    case "xs":
      return "sm";
    case "sm":
      return "md";
    default:
      return "lg";
  }
};

export const ButtonBase = forwardRef(
  (
    {
      colors,
      content,
      children,
      loading,
      endIcon,
      endIconPrefix,
      href,
      icon,
      iconProps,
      size,
      startIcon,
      startIconPrefix,
      ...props
    },
    ref
  ) => {
    const iconSize = getIconSize(size);

    return (
      <StyledButton
        component={href ? "a" : "button"}
        href={href}
        size={size}
        ref={ref}
        {...props}
      >
        {loading ? (
          <DotsSpinner color={colors?.color} />
        ) : (
          <>
            {startIcon && (
              <Icon
                icon={startIcon}
                prefix={startIconPrefix && startIconPrefix}
                size={iconSize}
                mr={2}
              />
            )}
            {icon && <Icon icon={icon} size={iconSize} {...iconProps} />}
            {content || children}
            {endIcon && (
              <Icon
                icon={endIcon}
                prefix={endIconPrefix && endIconPrefix}
                size={iconSize}
                ml={2}
              />
            )}
          </>
        )}
      </StyledButton>
    );
  }
);

ButtonBase.propTypes = ButtonBasePropTypes;

ButtonBase.defaultProps = ButtonBaseDefaultProps;
